@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body{
  font-family: 'Fira Mono', monospace;
  min-height: 100vh;
  overflow-x: hidden;
  padding : 2rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;

}

#root{
  width: 100%;

}
#app{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
  padding-bottom: 10rem;
  max-width: 1000px;
  margin: 0 auto;
}
img{
    width: 100%;
    object-fit: cover;
}
.label-input-icon{
    display: flex;
    flex-direction: column-reverse;

}
.input-icon{
    position: relative;
}
.input-icon.contain-start-icon input{
    padding-left: 3rem;
}
.input-icon.contain-end-icon input{
    padding-right: 3rem;
}
.input-icon .icon{
    display: block;
    width: 1.2rem;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

}
.input-icon .start-icon{
    left: 1.5rem;
}
.input-icon .end-icon{
    right: .5rem;
   
}
input::-webkit-input-placeholder {
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    color: #828282;
    outline: none;
}
input:-ms-input-placeholder {
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    color: #828282;
    outline: none;
}
input,label, input::placeholder {
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    color: #828282;
    outline: none;
}
label{
    color: inherit;
}
input{
    border-radius: 8px;
    border: 1px solid #828282;
    display: block;
    padding: 1.1rem .9rem;
    margin: .3rem 0;
    position: relative;
    min-width: 20rem;

}
input:hover{
    border: 1px solid #333333;
}
input:focus{
    border: 1px solid #2962FF;
}

.error input{
    border: 1px solid #D32F2F!important;
}
.error label{
    color: #D32F2F!important;
}
.disabled{
    background: #F2F2F2;
    border: 1px solid #E0E0E0!important;
}

.helper-text{
    color: #828282;
    font-size: .8rem;
}
.error .helper-text{
    color: #D32F2F;
}


